<template>
    <div class="mylike">
        <van-nav-bar 
        title="我的收藏"
        left-text="返回"
        left-arrow
        @click-left="back"
        />
        <Bg>
          <div class="mylike-box" v-if="mylikeData.length > 0">
              <div class="pro-item" v-for="(item,index) in mylikeData" :key="index">
                <Product :product="item">
                    <template #delete>
                        <div class="delete">
                            <van-icon name="delete-o" @click="deleteMyLike(item.pid)" size="18" />
                        </div>
                    </template>
                </Product>
              </div>
          </div>
          <div v-else>
            <van-empty description="没有收藏啦，赶紧去逛一逛！">
                <div class="guang" @click="goHome">逛一逛</div>
            </van-empty>
        </div>
        </Bg>
    </div>
</template>

<script>
    import Bg from '../components/Bg'
    import '../assets/less/mylike.less'
    import Product from '../components/Product'
    import {mapState,mapMutations} from 'vuex'

    export default {
        name:"MyLike",
        data(){
            return {
                mylikeData:[],
            }
        },
        methods:{
            back(){
                this.$router.push({name:"My"});
            },
            goHome(){
                this.$router.push({name:'Home'})
            },
            getMyLike(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAllLike',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 2000){
                        this.mylikeData = res.data.result;
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //取消收藏
            deleteMyLike(pid){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '删除中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/notlike',
                    data:{
                        appkey:this.appkey,
                        pid,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:3000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 900){
                        if(res.data.result == 1){
                            //删除
                            for(let i = 0;i < this.mylikeData.length;i++){
                                if(this.mylikeData[i].pid == pid) {
                                    this.mylikeData.splice(i,1);
                                    break;
                                }
                            }
                        }
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            ...mapMutations(["changeNewLike"])
        },
        components:{
            Bg,
            Product
        },
        created(){
            this.changeNewLike(false);
            this.getMyLike();
        },
        computed:{
            ...mapState(['isNewLike'])
        }
    }
</script>

